<template>
  <v-main class="black">
    <div class="pa-8"></div>
    <v-card id="pas" height="700" width="600" class="mx-auto card">
      <div class="pa-4"></div>
      <v-card class="pa-7" flat color="transparent">
        <v-container>
          <v-row justify="center" align="start" class="text-center">
            <v-img
              :src="require('@/assets/images/cmet_logo.webp')"
              max-width="60%"
              :min-width="'calc(70% - ' + $vuetify.application.top + 'px)'"
            />
          </v-row>
        </v-container>
      </v-card>

      <div class="pa-6"></div>

      <v-form ref="form" lazy-validation>
        <v-row justify="center">
          <v-col cols="10" md="8">
            <v-text-field
              dark
              single-line
              outlined
              rounded
              v-model="email"
              required
              clearable
              color="green accent-2"
            >
              <template v-slot:label>
                <p class="mx-4 white--text">E-mail</p>
              </template>
            </v-text-field>
          </v-col>
        </v-row>
        <v-row justify="center">
          <v-col cols="10" md="3">
            <p class="red--text text-center">{{ errorMsg }}</p>
          </v-col>
        </v-row>
        <v-row justify="center">
          <v-col class="text-center mb-6" cols="12">
            <v-btn
              type="submit"
              class="input"
              depressed
              rounded
              elevation="0"
              color="green accent-2"
              dark
              :width="$vuetify.application.top + 200 + 'px'"
              :max-height="$vuetify.application.top + 'px'"
              :min-height="$vuetify.application.top - 50 + 'px'"
              @click="submit"
            >
              <span style="font-size: 16px">Reset Password</span>
            </v-btn>
          </v-col>
          <v-col class="text-center" cols="12">
            <span class="white--text pt-6 mt-6">Don't have an account?</span>
            <v-btn
              class="login-btn pb-1"
              text
              depressed
              dark
              :to="{ name: 'Register' }"
            >
              <span style="font-size: 14px">Register</span>
            </v-btn>
          </v-col>
          <v-col class="text-center" cols="12">
            <span class="white--text pt-6 mt-6">Already have an account?</span>
            <v-btn
              class="login-btn"
              text
              depressed
              dark
              :to="{ name: 'Login' }"
            >
              <span style="font-size: 14px">Sign in</span>
            </v-btn>
          </v-col>
        </v-row>
      </v-form>
    </v-card>

    <v-card
      id="pasSucces"
      height="700"
      width="600"
      class="mx-auto card text-center d-none"
    >
      <div class="pa-4"></div>
      <v-card class="pa-7" flat color="transparent">
        <v-container>
          <v-row justify="center" align="start" class="text-center">
            <v-img
              :src="require('@/assets/images/cmet_logo.webp')"
              max-width="60%"
              :min-width="'calc(70% - ' + $vuetify.application.top + 'px)'"
            />
          </v-row>
        </v-container>
      </v-card>

      <div class="pa-16"></div>

      <p class="text-h6 white--text">Your letter has been sent</p>
    </v-card>
    <div class="pa-16"></div>
  </v-main>
</template>

<script>
import { forgetPwd } from "../api";

export default {
  data: () => ({
    date: null,
    menu: false,
    email: "",
    errorMsg: "",
  }),
  methods: {
    submit: function () {
      var params = new URLSearchParams();
      params.append("Email", this.email);

      forgetPwd(params)
        .then((result) => {
          console.log(result);
          if (result.data.success == true) {
            console.log(result.data.success);
            document.getElementById("pas").classList.add("d-none");
            document.getElementById("pasSucces").classList.remove("d-none");
          } else {
            this.errorMsg = result.data.error_msg;
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },
  },
};
</script>

<style lang="css" scoped>
.input {
  background: linear-gradient(to right, #00a199 0%, #00e6b4 100%);
}

.login-btn span {
  text-transform: capitalize;
  font-weight: normal;
}

.card {
  background: linear-gradient(to bottom, #002832 0%, #000000 100%);
}
</style>